'use strict';
import Vue from 'vue';
import axios from 'axios';
Vue.config.productionTip = false;
const slider = new Vue({
  el: '#worksSlider',
  data() {
    return {
      dataArr: [],
      slides: null,
      active: 1,
      prev: null,
      next: 2,
    };
  },
  mounted() {
    axios
      .get('/api/slider')
      .then(rs => {
        this.dataArr = rs.data.dataArr;
        this.slides = this.dataArr.length;
        this.prev = this.slides;
      })
      .catch(error => {
        console.log(error);
      });
  },
  methods: {
    move(amount) {
      let newActive;
      const newIndex = this.active + amount;
      this.prev = newIndex - 1;
      this.next = newIndex + 1;
      if(newIndex > this.slides) {
        newActive = 1;
        this.prev = this.slides;
        this.next = newActive + 1;
      }
      if(newIndex < 1) {
        newActive = this.slides;
        this.prev = newActive - 1;
        this.next = 1;
      }
      this.prev = (newIndex === 1) ? this.slides : this.prev;
      this.next = (newIndex === this.slides) ? 1 : this.next;
      this.active = newActive || newIndex;      
    },
  },
});
export default slider;


